.btn-calculator {
    width: 80px;
    height: 80px;
    background-color: #127a17;
    bottom: 0;
    right: 0;
    margin: 10px;
    z-index: 999;
    position: fixed;
    border: none;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    font-weight: 600;
    color: #fff;
    cursor: pointer;
}

.btn-calculator:hover {
    background-color: #0b460e;
}

.numbers {
    position: absolute;
    border: 1px solid #ce5a0c;
    background-color: #f3f3f3;
    border-radius: 20px;
    bottom: 0;
    right: 0;
    padding: 20px;
    z-index: 1001;
    margin: 0px 10px 90px 0px;
}

.numbers .buttonSubmit {
    background-color: #0b460e;
    width: 15%;
}

.add_results {
    border: 1px solid #0b460e;
    padding: 30px;
    border-radius: 2px;

}

.hide {
    padding: 5px;
    background-color: rgb(170, 170, 170);
    cursor: pointer;
    color: #0b460e;
    right: 0;
    position: absolute;
    margin: 10px 10px 0px 0px;

}

